$mainColor: #5EA2FF;
$treeColor: #71420C;
.tree--container {
    position: relative;
    width: 100%;
    min-height: 1080px;
    margin-left: auto;
    margin-right: auto; }
.tree--root {
    width: 100%;
    height: 290px;
    background: #11D70D;
    position: absolute;
    bottom: 0; }
.tree--flex {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    width: 650px;
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
.tree--lesson {
    background: #ffffff;
    width: 80px;
    height: 60px;
    padding: 4px 6px 4px 6px;
    border-radius: 10px; }
.tree--lesson-border {
    height: 52px;
    width: 68px;
    border: 3px solid $mainColor;
    box-sizing: border-box;
    border-radius: 10px;
    line-height: 19px;
    display: flex;
    align-items: center;
    justify-content: center; }
.tree--lesson--txt {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #000000; }
.tree--sphere {
    width: 120px;
    height: 42px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #FFFFFF;
    border-right: 20px solid $mainColor;
    box-shadow: -10px 10px 10px rgba(128, 128, 128, 0.4);
    border-radius: 10px;
    z-index: 1;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: #000000; }
.tree--sphere-introduction {
    margin-top: 90px; }
.tree--root--lesson {
    margin-top: 197px; }
.tree--root-img {
    position: absolute;
    z-index: 0;
    margin-top: -50px;
    margin-left: 8px; }
.tree--root--lists {
    position: absolute;
    margin-top: -90px;
    margin-left: 260px;
    z-index: 1; }
.tree--crown {
    background: linear-gradient(180deg, rgba(139, 188, 255, 0) 0%, #8BBCFF 100%);
    width: 100%;
    min-height: 790px; }
.tree--crown {
    position: realtive;
    display: flex;
    flex-direction: row;
    justify-content: center; }
.tree--crown--flex {
    bottom: 290px;
    display: flex;
    flex-direction: column-reverse;
    align-items: center; }
.tree--crown--line {
    position: absolute;
    background: $treeColor;
    box-shadow: -10px 10px 10px rgba(148, 148, 148, 0.4);
    border-radius: 20px;
    min-height: 550px;
    width: 12px;
    margin-top: 200px; }
.tree--crown--item {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: start;
    margin-left: 155px;
    margin-bottom: 80px; }
.tree--crown--item:nth-child(2n+1) {
    flex-direction: row-reverse;
    margin-left: -150px;
    margin-bottom: 50px; }
.tree--crown--item-line {
    position: relative;
    height: 12px;
    width: 80px;
    background: $treeColor;
    border-radius: 10px; }
.tree--crown--item--btn {
    background-color: $mainColor;
    box-shadow: -10px 10px 10px rgba(148, 148, 148, 0.4);
    border-radius: 50px;
    border: none;
    cursor: pointer;
    width: 70px;
    height: 70px;
    &:focus {
        outline: none; } }
.tree-crown-sphere {
    width: 140px;
    height: 42px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #FFFFFF;
    box-shadow: -10px 10px 10px rgba(128, 128, 128, 0.4);
    border-radius: 10px;
    z-index: 1;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 19px;
    color: #000000; }
.tree-sphere--form {
    position: absolute;
    display: none;
    background: none;
    height: 70px; }


